body,
html {
  width: 100%;
  margin: 0;
}

#typewriter {
  color: #b1a8b9;
  background-color: #0a192f;
  padding: 83px;
  font-family: "Rubik", sans-serif;
  font-size: 30px;
}
.Typewriter {
  font-family: "Bungee Shade", cursive;
  color: #11ffdb;
  text-align: left;
  font-size: 60px;
}

.MuiAvatar-root {
  font-family: "Gugi", cursive;
}

/* Use a media query to add a break point at 800px: */
@media screen and (max-width: 800px) {
  #typewriter {
    text-align: center;
    width: 100%;
    margin: auto;
    padding: 200px 2px 200px 2px;
    font-size: 20px;
  }
  .Typewriter {
    text-align: center;
    font-size: 30px;
  }
}

#about {
  background-color: #0a192f;
  display: flex;
  justify-content: space-around;
  padding: 20px;
  font-family: "Rubik", sans-serif;
  padding-bottom: 70px;
}
.profile_pic {
  margin-right: 30px;
  width: 22%;
  height: 270px;
  border-radius: 5px;
  box-shadow: 15px 17px 0px 2px rgba(0, 201, 170, 1);
}

#about h1 {
  color: #00c9aa;
}
.about_text {
  color: #b1a8b9;
  width: 80%;
  font-size: 20px;
  font-family: "Rubik", sans-serif;
  margin: auto;
}

b {
  color: #00c9aa;
}

.skills {
  background-color: #0a192f;
  padding: 5px;
}

.skills span {
  color: #00c9aa;
  font-size: 20px;
  font-weight: bolder;
}

@media screen and (max-width: 800px) {
  #about {
    display: flex;
    flex-direction: column;
    justify-content: space-around;
    padding: 0px;
    font-family: "Rubik", sans-serif;
    font-size: 10px;
  }
  .profile_pic {
    margin: auto;
    width: 70%;
    height: auto;
    margin-top: 10px;
    margin-bottom: 50px;
  }
  .about_text {
    font-size: 12px;
  }
}

#work {
  background-color: #0a192f;
  font-family: "Rubik", sans-serif;
  padding: 20px;
}

#work h1 {
  color: #00c9aa;
  font-size: 2em;
}

.work_timeline {
  font-family: "Rubik", sans-serif;
}

#project {
  background-color: #0a192f;
  font-family: "Rubik", sans-serif;
  padding: 20px;
}

#project h1 {
  color: #00c9aa;
  font-size: 2em;
  margin: auto;
  text-align: center;
  padding: 5px;
}

#contact {
  background-color: #0a192f;
  font-family: "Rubik", sans-serif;
  padding: 20px;
}

#contact h1 {
  text-align: center;
  color: #00c9aa;
  font-size: 2em;
  margin: auto;
  text-align: center;
  padding: 10px;
  border-bottom: 2px solid #00c9aa;
}

#contact a {
  color: #00c9aa;
  margin: auto;
  font-family: "Rubik", sans-serif;
  font-size: 1.5em;
  text-decoration: none;
}


